import { useQuery } from 'react-query';
import { useDataSetId } from '../use-context-selection/index.js';
export default function useCategoryCombination() {
  const [id] = useDataSetId();
  const params = {
    fields: ['categoryCombo[isDefault,displayName, categories[id, displayName, categoryOptions[id, displayName]]]']
  };
  const queryKey = ['dataSets', {
    id,
    params
  }];
  const {
    isIdle,
    isLoading: loading,
    error,
    data
  } = useQuery(queryKey, {
    enabled: !!id,
    select: data => data.categoryCombo
  });
  return {
    called: !isIdle,
    loading,
    error,
    data
  };
}